// -----------------------------------------------------------------------------
// This file contains all styles related to in-page table of contents.
// -----------------------------------------------------------------------------

.toc-section {
  @include text-size("caption");

  &__heading {
    @include sub-heading;
    @include text-size("h4");
    margin-bottom: $space-text;
  }

  ul {
    list-style-type: none;
    padding-left: 2 * $space-unit; // 16px
    margin: 0;
  }

  > ul {
    padding-left: 0;
  }

  li {
    margin: $space-unit 0;
  }

  /* Make ToC sticky when used in layouts on medium and large screens */
  @include respond-to("md") {
    position: sticky;
    top: $space-lg;
  }
}
