// -----------------------------------------------------------------------------
// This file contains styles that are specific to the feature.html layout.
// -----------------------------------------------------------------------------

.feature {
  /* Mobile-first: small screen & default styles */
  section {
    .container {
      /* Set up full-width i.e. one column grid layout for small screens */
      display: grid;
      grid-template-columns: 1fr;
      grid-row-gap: $space-vertical;

      /* Remove margin on children because grid-row-gap provides vertical space */
      > * {
        margin: 0;
      }
    }

    /* Make alternating sections "contrast sections" with a different bg colour */
    &:nth-of-type(even) {
      @include contrast-section;
    }
  }

  /* Make banner padding larger for feature pages */
  .banner .container {
    padding-top: $space-xl; // 160px
    padding-bottom: $space-xl; // 160px
  }

  /* Large screen styles */
  @include respond-to("lg") {
    /* Make banner padding even larger for feature pages on large screens */
    .banner .container {
      padding-top: $space-unit * 20; // 160px
      padding-bottom: $space-unit * 20; // 160px

      h1 {
        @include text-size("title");
        margin-bottom: $space-vertical;
      }

      p.lead {
        @include text-size("h2");
      }
    }

    /* Make container padding larger for feature pages on large screens */
    section .container {
      padding-top: $space-xl;
      padding-bottom: $space-xl;
    }

    /* Setup default uneven two-column grid layout unless .full class is applied */
    section:not(.full) .container {
      grid-template-columns: 25% calc(75% - #{$space-xl});
      grid-column-gap: $space-xl;
      grid-template-areas:
        "header  article"
        "footer  article"
        "spacer  article";

      > header {
        grid-area: header;
      }

      > article {
        grid-area: article;
      }

      > footer {
        grid-area: footer;
      }
    }
  }
}
