// -----------------------------------------------------------------------------
// This file contains styles that are specific to the detail.html layout.
// -----------------------------------------------------------------------------

.detail {
  /* Make page container grow to fill vertical height on short pages */
  flex: 1 1 auto;
  display: flex;
  flex-direction: column;

  /* Use filler container to continue gold rule while filling vertical height */
  .filler.container {
    padding-top: 0;
    padding-bottom: 0;
    flex: 1 1 auto;  
  }

  > .grid {
    /* Mobile-first grid - all elements take up full-width */
    grid-template-columns: 100%;
    grid-template-rows: auto;
    grid-row-gap: $space-lg;

    /* Remove any top margin on top of any grid children or the first element inside them */
    > * {
      margin-top: 0;

      & > *:first-child {
        margin-top: 0;
      }
    }

    /* Grid-based default page layout on medium screens */
    @include respond-to("md") {
      grid-template-columns: repeat(8, minmax(0, 1fr));
      /* minmax for first row needed for case where sidebars are longer than main content
      we want the article to expand and not the header*/
      grid-template-rows: minmax(0, auto) auto auto;
      grid-template-areas: 
        "hd hd hd hd hd hd hd hd"
        "pa pa pc pc pc pc pc pc"
        "ca ca ca ca ca ca ca ca";

      > .detail__header {
        grid-area: hd;
      }

      > .detail__page-aside {
        grid-area: pa;
        /* Fill available height so sticky toc inside has bounded container to scroll down */
        height: 100%;
      }

      > .detail__page-content {
        grid-area: pc;
      }

      > .detail__collection-aside {
        grid-area: ca;
      }
    }

    /* Grid-based default page layout on large screens */
    @include respond-to("lg") {
      grid-template-columns: repeat(12, minmax(0, 1fr));
      /* minmax for first row needed for case where sidebars are longer than main content
       we want the article to expand and not the header */
      grid-template-rows: minmax(0, auto) auto;
      grid-template-areas: 
        "hd hd hd hd hd hd hd hd hd hd ca ca"
        "pa pa pc pc pc pc pc pc pc pc ca ca";

      > .detail__header {
        grid-area: hd;
      }

      > .detail__page-aside {
        grid-area: pa;
      }

      > .detail__page-content {
        grid-area: pc;
      }

      > .detail__collection-aside {
        grid-area: ca;
      }
    }
  }
}
