.logo-favicon {
  display: inline-block;
  /* Hard-coded favicon logo display size */
  width: 40px;
  height: 40px;
  margin: $space-xs;
  background-image: favicon-logo();
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center center;
}
