// -----------------------------------------------------------------------------
// This file imports all styles needed for main website theme. It should not
// contain anything but `@import` and comments.
// -----------------------------------------------------------------------------

@charset "UTF-8";

// 1. Configuration and helpers
@import
  'abstracts/variables',
  'abstracts/functions',
  'abstracts/colors',
  'abstracts/mixins';
  
// 2. Vendors
@import
  'vendors/normalize',
  'vendors/anchor_headings';

// 3. Base stuff
@import
  'base/base',
  'base/button',
  'base/form',
  'base/grid',
  'base/helpers',
  'base/icons',
  'base/images',
  'base/typography',
  'base/table';

// 4. Partials generate parts of a page
@import
  'partials/banner',
  'partials/breadcrumb',
  'partials/collection-nav',
  'partials/cover-image',
  'partials/footer',
  'partials/header',
  'partials/logo-favicon',
  'partials/logo-lockup',
  'partials/logo-secondary',
  'partials/nav-menu',
  'partials/nav-submenu',
  'partials/pagination',
  'partials/person-summary',
  'partials/related-pages',
  'partials/search-menu',
  'partials/tags-nav',
  'partials/toc-section',
  'partials/trap-focus';

// 5. Components
@import
  'components/callout-box',
  'components/card',
  'components/collection-listing',
  'components/filter-buttons',
  'components/segmented-control',
  'components/select-control',
  'components/select-typeahead',
  'components/tag';

// 6. Page-specific styles
@import
  'pages/default',
  'pages/detail',
  'pages/feature';

