// -----------------------------------------------------------------------------
// This file contains all styles related to the trap focus button
// -----------------------------------------------------------------------------

.trap-focus {
  /* Visually hide (even when focussed) but allow it to be tabbed to */
  clip: rect(0 0 0 0);
  clip-path: inset(50%);
  height: 1px;
  overflow: hidden;
  position: absolute;
  white-space: nowrap;
  width: 1px;
  padding: 0;
  /* Override button styling */
  padding: 0;
  border: 0;
  /* Remove size set by width and height so element doesn't take up space */
  margin: -1px;
}
