// -----------------------------------------------------------------------------
// This file contains all styles related to page collection nav.
// -----------------------------------------------------------------------------

.collection-nav {
  @include text-size("caption");

  &__heading {
    @include sub-heading;
    @include text-size("h3");
    margin-bottom: $space-text;

    a {
      text-decoration: none;

      svg.icon {
        @include transition("sm", transform);
      }

      @include on-event {
        svg.icon {
          transform: translateX(-1 * $space-xs);
        }
      }
    }
  }

  > a {
    display: block;
    margin: $space-unit 0;

    &.current-page {
      text-decoration: none;
      color: $primary-color;
    }
  }
}
