// -----------------------------------------------------------------------------
// This file contains all styles related to the person page person summary.
// -----------------------------------------------------------------------------

.person-summary {
  grid-column-gap: $space-lg;
  grid-row-gap: $space-vertical;

  &__image {
    grid-column: 1 / 2;
  }

  &__info {
    grid-column: 2 / -1;
    grid-row-gap: $space-text;
  }

  .warn-box {
    margin: 0;
  }

  @include respond-to("md") {
    grid-template-columns: repeat(4, 1fr);
  }

  @include respond-to("lg") {
    grid-template-columns: repeat(5, 1fr);
  }
}
