// -----------------------------------------------------------------------------
// This file contains all styles related to the site navigation menu.
// -----------------------------------------------------------------------------

/**
* Mobile first - small screen hamburger menu styling
*/
.nav-menu {
  @include off-canvas-menu;
  left: -1 * $off-canvas-menu-width;
  box-shadow: -8px 0 8px $box-shadow-color;
  visibility: hidden;
  @include z-index("nav-menu");
  @include transition("lg", left, box-shadow, visibility);

  &__nav {
    width: 100%;
  }

  &__brand {
    display: block;
    /* Use margins to position brand area in menu header */
    margin: $space-unit $space-sm;
    /* Make brand area fit secondary master brand logo + subbrand lockup */
    width: 32 * $space-unit; // 256px
    height: 6 * $space-unit; // 48px
    text-decoration: none;
    /* Remove inline space between logo & lockup */
    font-size: 0;

    @include on-event {
      /* Override default a styles */
      color: $text-color;
    }
  }

  &__close {
    position: absolute;
    top: $space-unit;
    right: 0;
  }

  &__menu.container {
    list-style: none;
    border-bottom: 1px solid $primary-color;
    /* Override .container values */
    padding: 0;
    width: 100%;
  }

  &__menu-item {
    border-top: 1px solid $primary-color;
    position: relative;

    &.current-page {
      .nav-menu__menu-link {
        background-color: $primary-bg-color;
        box-shadow: inset 4px 0 0 0 $primary-color;
      }
    }

    &.current-page-inside {
      .nav-menu__menu-link {
        box-shadow: inset 4px 0 0 0 $primary-color;
      }
    }
  }

  &__menu-link {
    @include text-component;
    width: 100%;
    background-color: transparent;
    text-decoration: underline;
    text-decoration-color: transparent;

    @include transition(
      "short",
      background-color,
      color,
      text-decoration-color
    );

    @include on-event {
      /* override base a style */
      color: $text-color;
      background-color: $primary-bg-color;
      text-decoration-color: $text-color;
    }
  }

  &__menu-link-caret {
    display: none;
  }

  /**
    * Main nav menu toggle behaviour
    * :target for non-JavaScript
    * [aria-expanded] will be used if/when JavaScript is added to improve interaction, though it's completely optional.
    */
  &:target,
  &[aria-expanded="true"] {
    display: block;
    left: 0;
    box-shadow: 0 0 8px $box-shadow-color;
    visibility: visible;
    outline: none;

    .nav-menu__close {
      z-index: 1001;
    }

    & + .nav-menu-overlay {
      opacity: 1;
      visibility: visible;
    }
  }

  & + .nav-menu-overlay {
    @include overlay;
    @include z-index("nav-menu-overlay");
    opacity: 0;
    visibility: hidden;
    @include transition("lg", opacity, visibility);
  }
}

/**
* Larger screen styling - horizontal nav menu
*/
@include respond-to("lg") {
  .nav-menu {
    /* Reset off canvas menu properties */
    background: none;
    box-shadow: none;
    position: relative;
    /* Needed so nav submenu can show */
    overflow: initial;
    left: auto;
    right: auto;
    top: auto;
    width: auto;
    height: auto;
    display: flex;
    justify-content: space-between;
    visibility: visible;
    /* Show as secondary header bar with gold tint bg */
    background-color: $primary-bg-color;

    /* Don't show nav menu logo there's already one in the primary header */
    &__brand {
      display: none;
    }

    &__close {
      display: none;
    }

    &__menu.container {
      border: none;
      display: flex;
      /* Custom left amount to make .container contents line up with logo text */
      padding-left: 4 * $space-unit;
      /* Reset regular large screen .container right padding */
      padding-right: 2 * $space-unit;
      /* Reset width to large screen .container width */
      width: calc(100% - #{8 * $space-unit}); // 100% - 64px;
    }

    &__menu-item {
      border: none;
      display: inline-block;
      /* Needs to be static in order to position the submenu inside it relative to the page */
      position: static;

      /* Current page styles should come before hover/focus styles so background color has correct inheritance */
      &.current-page,
      &.current-page-inside {
        .nav-menu__menu-link {
          /* Give the link a gold bar on top if it's the current page or the parent of the current page */
          box-shadow: inset 0 4px 0 0 $primary-color;
          /*
          * Reserve the background colour shading to indicate hover / submenu open
          * Overwrites small screen style which also shades the current page
          */
          background-color: transparent;
        }
      }

      /* Make main menu link look hovered / open when: */
      /* any part of the menu-item is hovered, which includes the submenu */
      &:hover,
      /* any descendent of the menu-item is focussed, which includes both main menu & submenu links, and toggle checkbox */
      &:focus-within {
        .nav-menu__menu-link {
          background-color: $white;
        }
      }
    }

    &__menu-link {
      width: auto;

      /* Different on-event colors because of gold menu bg / white menu link highlight on large screens */
      @include on-event {
        color: $primary-color;
        text-decoration-color: $primary-color;
      }
    }

    &__menu-link-caret {
      display: inline-block;
      width: $space-sm; // 8px
      margin-left: $space-xs; // 4px
    }

    /* Hide focus traps on large screens because nav is visible and part of page rather than a dialog ` */
    .trap-focus {
      display: none;
    }
  }
}
