// -----------------------------------------------------------------------------
// This file contains all styles related to the site search menu.
// -----------------------------------------------------------------------------

.search-menu {
  @include off-canvas-menu;
  right: -1 * $off-canvas-menu-width;
  box-shadow: 8px 0 8px $box-shadow-color;
  visibility: hidden;
  @include transition("lg", right, box-shadow, visibility);
  @include z-index("search-menu");
  padding: $space-md $space-sm;

  &__title {
    margin-top: 0;
    margin-right: $space-clickable;
  }

  &__close {
    position: absolute;
    /* Make icon vertically align with title */
    top: $space-unit; // 8px;
    right: 0;
  }

  form {
    padding-top: 0;
    border-top: none;
  }

  .search-button {
    width: 100%;
  }

  /**
  * Search toggle behaviour
  * :target for non-JavaScript
  * [aria-expanded] will be used if/when JavaScript is added to improve interaction, though it's completely optional.
  */
  &:target,
  &[aria-expanded="true"] {
    display: block;
    right: 0;
    box-shadow: 0 0 8px $box-shadow-color;
    visibility: visible;
    outline: none;

    & + .search-menu-overlay {
      opacity: 1;
      visibility: visible;
    }
  }

  & + .search-menu-overlay {
    @include overlay;
    @include z-index("search-menu-overlay");
    opacity: 0;
    visibility: hidden;
    @include transition("lg", opacity, visibility);
  }
}
