// -----------------------------------------------------------------------------
// This file contains all styles related to the page cover image.
// -----------------------------------------------------------------------------

$COVER_IMAGE_MAX_HEIGHT: 32rem; // 512px

/* Uses figure selector because styles assume cover image is a figure with figcaption */
figure.cover-image {
  /* Make figure full width on small screens */
  margin-left: -1 * $space-md;
  margin-right: -1 * $space-md;

  img {
    max-height: $COVER_IMAGE_MAX_HEIGHT;
    object-fit: cover;
  }

  figcaption {
    /* Give caption its normal margins back */
    margin-left: $space-md;
    margin-right: $space-md;
  }

  @include respond-to("md") {
    /* Pull the figure right so it sits under gold rule */
    margin-left: -1 * $space-xl;
    margin-right: 0;

    figcaption {
      /* Give caption its normal margins back */
      margin-left: $space-xl;
      margin-right: 0;
      /* Right align the caption */
      text-align: right;
    }
  }
}
