// -----------------------------------------------------------------------------
// This file contains all styles related to the page banner.
// -----------------------------------------------------------------------------

.banner {
  color: $text-color-inverted;
  text-decoration: none;
  position: relative;
  background-image: var(--background);
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;

  /* Place a light transparent black overlay over the image so white text has enough contrast */
  &:before {
    content: "";
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    background-color: $image-overlay-color;
  }

  &__content {
    /* Take up the whole row on small screens */
    grid-column: 1 / -1;
  }

  /* Image caption for banner background image */
  &__caption {
    @include text-size("caption");
    position: absolute;
    right: $space-sm;
    bottom: $space-sm;
    color: $banner-caption-color;
  }

  @include respond-to("lg") {
    .container {
      padding-top: $space-xl;
      padding-bottom: $space-xl;
    }

    &__content {
      /* Use 2 of 3 grid columns on large screens */
      grid-column: 1 / 3;
    }
  }
}
