// -----------------------------------------------------------------------------
// This file contains all styles related to the logo lockup
// -----------------------------------------------------------------------------

.logo-lockup {
  display: inline-block;
  margin-left: 2 * $space-unit;
  width: 14 * $space-unit;
  /* Make lockup font size visually match logo-secondary font size & line height */
  font-size: 15px;
  line-height: 15px;
  /* Make lockup vertically align to top of logo-secondary */
  vertical-align: top;
  margin-top: 1px;
}
